.banner__sep {
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 18vw;
    background: linear-gradient(rgb(240, 164, 0), rgb(240, 184, 0));
    margin: 0;
}

.banner__sep__stars {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 50vw;
    font-size: 4.5vw;
    opacity: 0;
    color: white;
}

.banner__box {
    margin-top: -19.2vw;
    width: 100%;
}

.banner__box__title {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    height: 47vw;
    margin: 0;
    background-color: black;
}

.banner__box__title__letter {
    display: inline-block;
    font-size: 15vw;
    font-weight: 400;
    letter-spacing: 2vw;
    color: rgb(240, 184, 0);
}

.banner__box__subTitle {
    position: relative;
    justify-content: center;
    text-align: center;
    width: 100%;
    margin-top: -10vw;
    margin-bottom: 0;
    letter-spacing: 1.5vw;
    font-size: 5vw;
    font-weight: 500;
    color: rgb(240, 184, 0);
    opacity: 0;
}

.banner__box__subTitle__play {
    margin-top: 5vw;
    font-size: 12vw;
    cursor: pointer;
    color: rgb(240, 184, 0);
    transition: color .5s;
}

.banner__box__subTitle__play:hover {
    color: rgb(240, 136, 0);
    transition: color .5s;
}

.banner__box__subTitle__play:active {
    color: rgb(248, 199, 93);
}

.banner__pic-box {
    position: relative;
    width: 100%;
    height: 120vw;
    margin: 0;
    background: transparent;
}

.box {
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 0;
    width: 100%;
    min-height: 140vw;
    height: auto;
    background-color: black;
}

section>div>header>h1 {
    position: relative;
    top: 0;
    padding-top: 5vw;
    margin: 0;
    width: 100%;
    height: 23vw;
    background: linear-gradient(rgb(240, 164, 0), rgb(240, 184, 0));
    font-size: 10vw;
    font-weight: 400;
    letter-spacing: .4vw;
    color: white;
}

section>div>p {
    position: relative;
    margin: 10vw;
    text-align: left;
    font-weight: 200;
    font-size: 5vw;
    line-height: 7vw;
    letter-spacing: .4vw;
    color: white;
}

section>div>p>span {
    font-weight: 600;
    color: rgb(240, 179, 48)
}